html, body{
    margin: 0;
    padding: 0;
    
    
}
.wrapper{
    box-sizing: border-box;
    margin: 20px;
    border: 10px solid #e1e7ee;
    min-width: 900px;
    max-width: 1900px;
    height: 90vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    
} 
#app {
    margin: 0 40px 40px;
       
}
header{
    display: flex;
    justify-content: space-between;
}
.left{
    display: flex;
    margin-left: 70px;
    align-items: center;
    
    a{
        padding-top: 30px; 
    }
}
.right{
 display: flex;
 align-items: center;
 margin-right: 60px;
 }

a{
    font-size:20px;
    display: block;
    margin: 50px 0;
    padding: 10px;
    color: #8b908e;
    
}
.order{
    font-size:20px;
   padding-top: 20px;

}
.tables {
    height: 100px;
   display: flex;
   overflow: hidden;  
   border: 2px solid #e1e7ee;
   margin: 0 30px;
   
  
   &:hover{
        overflow-y: scroll;   
   } 
}

.table1 {
    width: 50%;
    height: 100%;
    
   
    .graphic{
        position: absolute;
        height: 100%;
        background-color: #dcf4e7;
        right: 0; 
        top:0;
        opacity: 0.5;
        z-index: -1;
    }
    
    
    
    .data div {
       color:#8b908e;
       &:hover{
        color:#8b908e;
       }
   }
    
 }
 .table2 {
    width: 50%;
   
       .graphic{
        position: absolute;
        height: 100%;
        background-color: #ffe5e8;
        left: 0; 
        top:0;
        opacity: 0.5;
        z-index: -1;
    }
  
   
    .data div {
       color:#8b908e;
   }
    
 }
.data {
    display: flex;
   
    div{
        width: 25%;
        text-align: center;
        font-size: 16px;
        font-weight:bold;
        padding: 0 5px;
       
    }
}
.data:hover {
    background-color: #e5e9f2;
}


.lables{
    display:flex;
    border: 2px solid #e1e7ee;
    margin: 0 70px;
    padding: 20px 0;
    .label1{
        width: 50%;
        display: flex;
              
        div{
        width: 25%; 
        text-align: center;
        font-size: 20px;
        font-weight:bold;
        
        }
    } 
    .label2{
        width: 50%;
        display: flex;
       
        div{
            width: 25%; 
            text-align: center;
            font-size: 20px;
            font-weight:bold;
            
        } 
    }
} 
@media screen and (max-width: 1100px){
    .data div  {
        font-size: 14px;
    }
}
@media screen and (max-width: 1000px){
    .data div  {
        font-size: 12px;
    }
}